<template>
  <div class="container">
    <div class="card">
      <div class="card-header">
        <h1 class="h1 mb-0 text-center">Perwalian Mahasiswa STTbandung</h1>
      </div>
      <div class="card-body">
        <div class="row">
          <div class="col-xs-12 col-md-5">
            <form class="form">
              <div class="">
                <label class="form-label">NPM</label>
                <input
                  type="text"
                  class="form-control col-sm-12"
                  v-model="form.datanpm"
                  disabled
                />
              </div>
              <div class="form-group">
                <label class="form-label">Nama Lengkap</label>
                <input
                  type="text"
                  class="form-control col-sm-12"
                  placeholder="Nama Lengkap"
                  v-model="form.dataname"
                  disabled
                />
              </div>
              <div class="form-group">
                <label class="form-label">Prodi</label>
                <input
                  type="text"
                  class="form-control col-sm-12"
                  placeholder="Prodi"
                  v-model="form.dataprodi"
                  disabled
                />
              </div>
              <div class="form-group">
                <label class="form-label">Semester</label>
                <input
                  type="text"
                  class="form-control col-sm-12"
                  placeholder="Semester"
                  v-model="form.datasemester"
                  disabled
                />
              </div>

              <select
                v-if="dataPerwalian.semester > 3"
                class="form-control col-12 col-md-12 h-50 mb-5"
                id="exampleFormControlSelect1"
                v-model="form.concentration_id"
              >
                <option disable>Pilih Konsentrasi</option>
                <option value="1">Computer Information Development</option>
                <option value="2">Computer Network Security</option>
              </select>
              <button
                type="button"
                class="btn btn-success"
                data-toggle="modal"
                data-target="#modalConfirm"
              >
                Ajukan
              </button>
            </form>
          </div>
          <div class="col-xs-12 col-md-7">
            <strong>Langkah-Langkah Perwalian</strong>
            <ul class="timeline mt-2 mb-0 text-left">
              <li class="timeline-item">
                <strong>Mahasiswa</strong>
                <p>
                  Pastikan sebleum klik tombol "Perwalian Sekarang", data
                  semester anda benar. Apabila tidak sesuai hubungi Bagian Gesit
                </p>
              </li>
              <li class="timeline-item">
                <strong>Dosen Wali</strong>
                <p>
                  Dosen Wali akan memberikan ACC perwalian mahasiswa. Apabila
                  Dosen Wali belum ACC, silahkan hubungi Dosen Wali.
                </p>
              </li>
              <li class="timeline-item">
                <strong>Keuangan</strong>
                <p>
                  Keuangan akan memberikan ACC perwalian mahasiswa, ketika tidak
                  terdapat tunggakan keuangan. Apabila ada tunggakan segera
                  lakukan pembayaran atau silahkan hubungi Bagian Keuangan.
                </p>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="allmodal">
        <!-- The Modal -->
        <div class="modal fade" id="modalConfirm">
          <div class="modal-dialog">
            <div class="modal-content">
              <!-- Modal body -->
              <div class="modal-body">
                <h1 class="h1">Lanjutkan?</h1>
                <p>Berikut Adalah data Perwalian Anda</p>
                <div class="table-responsive">
                  <table class="table" :hidden="isHidden">
                    <thead>
                      <tr>
                        <td>Npm</td>
                        <td>Nama</td>
                        <td>Prodi</td>
                        <td>Semester</td>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>{{ dataPerwalian.npm }}</td>
                        <td>{{ dataPerwalian.full_name }}</td>
                        <td>{{ dataPerwalian.faculty_ina }}</td>
                        <td>{{ dataPerwalian.semester }}</td>
                      </tr>
                    </tbody>
                  </table>
                  <div class="text-center" :hidden="!isHidden">Loading Data . . .</div>
                </div>
              </div>
              <!-- Modal footer -->
              <div class="modal-footer">
                <div class="spinner-border text-success" style="width: 14px;height: 14px;padding:0" v-if="isDisabled == true">
                    <span class="sr-only">Loading...</span>
                  </div>
                <button
                  type="submit"
                  class="btn btn-success"
                  id="submit"
                  @click="submit"
                  :disabled="isDisabled"
                >
                  Lanjutkan
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- <div class="card">
      <div class="card-header">
        <h1 class="h1 mb-0 text-center">SELAMAT KAMU SUDAH PERWALIAN</h1>
      </div>
    </div> -->
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import axios from "axios";
export default {
  name: "Step1",
  inject: ["notyf"],
  data() {
    return {
      form: {
        datanpm: "",
        dataname: "",
        dataprodi: "",
        datasemester: "",
        school_year_id: "",
        concentration_id: 0,
      },
      isDisabled: false,
      isHidden:false
    };
  },
  computed: {
    ...mapGetters({
      dataPerwalian: "onepage/getPerwalian",
    }),
  },
  methods: {
    ...mapActions({
      perwalian: "onepage/perwalian",
    }),
    getPerwalian() {
      this.perwalian();
    },
    async submit() {
      try {
        let dataperwalian = {
          npm: this.form.datanpm,
          semester: this.form.datasemester,
          school_year_id: this.form.school_year_id,
          concentration_id: this.form.concentration_id,
        };

        let response = await axios.post(
          "/oasis/preguardianship",
          dataperwalian
        );
        this.getPerwalian();
        this.isDisabled = true;
        this.isHidden = true
        setTimeout(() => {
          window.location.href = "/home";
        }, 5000);
    
        if (response.data.meta.status === "success") {
          this.notyf.success({
            message: response.data.meta.message,
            dismissible: false,
            duration: 4000,
          });
        } else {
          this.notyf.error({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        }

        return response;
      } catch (error) {
        this.notyf.error({
            message: "Maaf, Cek Kembali Data Anda",
            dismissible: false,
            duration: 5000,
          });
      }
    },
  },

  mounted() {
    this.getPerwalian();
    this.form.datanpm = this.dataPerwalian.npm;
    this.form.dataname = this.dataPerwalian.full_name;
    this.form.dataprodi = this.dataPerwalian.faculty_ina;
    this.form.datasemester = this.dataPerwalian.semester;
    this.form.school_year_id = this.dataPerwalian.school_year_id;
    $("main").even().removeClass("content");
  },
  components: {},
};
</script>

<style >
.card {
  box-shadow: none !important;
}
.navbar {
  box-shadow: none !important;
}
.main{
  background: white
}
</style>